@charset "UTF-8";
header {
  background-color: #0B0A0D;
  height: 22vh;
}
header .custom-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
header .custom-header a {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
header .custom-header a img {
  width: 8rem;
  height: auto;
}
header .custom-header a p {
  color: white;
  font-weight: bold;
  font-size: 0.6rem;
  margin: 0;
}
header .custom-header nav ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 40px;
}
header .custom-header nav ul li {
  margin-left: 1rem;
}
header .custom-header nav ul li a {
  font-weight: bold;
  color: white;
  text-decoration: none;
}

#s__home {
  width: 100%;
  height: calc(100vh - 9.6rem);
  background-color: red;
  background-image: url("../assets/bg/bg.jpg");
  background-size: cover;
}
#s__home .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  color: white;
}
#s__home .container h1 {
  color: white !important;
}
#s__home .container .filter {
  background-color: rgba(0, 0, 0, 0.5);
  width: 70rem;
  padding: 1rem;
  border: 1px solid red;
}
#s__home .container .filter p {
  margin-bottom: 1rem;
}
#s__home .container .filter form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#s__home .container .filter form .filters {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 1rem; /* Adiciona espaço entre os itens */
}
#s__home .container .filter form .filters .filter-item {
  width: 10rem;
}
#s__home .container .filter form .filters .filter-item label {
  display: block;
  margin-bottom: 0.5rem;
}
#s__home .container .filter form .filters .filter-item input,
#s__home .container .filter form .filters .filter-item select {
  width: 100%;
}